<template>
  <div>
    <van-row>
      <van-col class="l" span="6">
        <van-sidebar v-model="activeKey">
          <van-sidebar-item title="杭州" />
          <van-sidebar-item title="广州" />
          <van-sidebar-item title="深圳" />
          <van-sidebar-item title="北京" />
        </van-sidebar>
      </van-col>
      <van-col class="r" span="18">
        <van-swipe :autoplay="3000">
          <van-swipe-item v-for="(image, index) in images" :key="index">
            <img v-lazy="image" />
          </van-swipe-item>
        </van-swipe>
        <van-tabs @click="onClick" v-model="activeName">
          <van-tab v-for="(item) in tit" :key="item.id" :title="item.text" :name="item.id">
            <van-grid :border="false" :column-num="3" class="goodslist">
              <van-grid-item
                v-for="(item) in goodslist"
                :key="item._id"
                @click="gotoDetail(item._id)"
              >
                <van-image :src="item.img_url" />
                <h4>{{item.goods_name}}</h4>
                <p class="price">
                  <span>{{item.sales_price}}</span>
                </p>
              </van-grid-item>
            </van-grid>
          </van-tab>
        </van-tabs>
      </van-col>
    </van-row>
  </div>
</template>
<script>
import Vue from "vue";
import {
  Lazyload,
  Col,
  Row,
  Sidebar,
  SidebarItem,
  Swipe,
  SwipeItem,
  Tab,
  Tabs,
} from "vant";
Vue.use(Tab);
Vue.use(Tabs);
Vue.use(Lazyload);
Vue.use(Swipe);
Vue.use(SwipeItem);
Vue.use(Sidebar);
Vue.use(SidebarItem);
Vue.use(Col);
Vue.use(Row);
export default {
  data() {
    return {
      activeKey: 0,
      activeName: 1,
      goodslist: [],
      tit: [
        {
          id: 1,
          text: "普通香水",
        },
        {
          id: 2,
          text: "会员香水",
        },
        {
          id: 3,
          text: "至尊香水",
        },
      ],
      images: [
        "/img/banner1.jpg",
        "/img/banner2.jpg",
        "/img/banner3.jpg",
        "/img/banner4.jpg",
        "/img/banner5.jpg",
        "/img/banner6.jpg",
      ],
    };
  },
  methods: {
      //跳到商品商情
    gotoDetail(id) {
      this.$router.push({
        name: "Goods",
        params: {
          id,
        },
      });
    },
    async onClick(name, title) {
      console.log("数据", name, title);
      const {
        data: { data: goodslist },
      } = await this.$request.get("goods", {
        params: {
          total: 0,
          size: 15,
          page: name,
        },
      });
      this.goodslist = goodslist;
    },
  },

  async created() {
    this.onClick();
  },
};
</script>
<style lang="scss" scoped>
.van-grid-item__content {
  width: 80px;
}
.l {
  //   background-color: red;
}
.r {
  //   background-color: yellow;
  //   padding-left: 5px;
}
.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  line-height: 150px;
  text-align: center;
  background-color: #39a9ed;
}
.van-swipe-item {
  display: flex;
  width: 274px;
  height: 87px;
  img {
    margin: auto;
    width: 274px;
    height: 87px;
  }
}
</style>